export const FC_AdminApplicationPositionDistinctCategory = (
  thisPosition: PositionAPI,
  documentCategories: DocumentReducerInterface
): {
  PositionsTemp: PositionRequirementTableInterface[];
  DistinctCategoriesTemp: PositionRequirementTableInterface[];
  DistinctSubCategoriesTemp: PositionRequirementTableInterface[];
} => {
  let PositionsTemp: PositionRequirementTableInterface[] = [];
  let DistinctCategoriesTemp: PositionRequirementTableInterface[] = [],
    DistinctCategoriesIds: string[] = [];
  let DistinctSubCategoriesTemp: PositionRequirementTableInterface[] = [],
    DistinctSubCategoriesIds: string[] = [];
  // GET REQUIREMENTS OF THE CURRENT POSITION ====================
  let requirements = thisPosition.requirements;
  // LOOP THROUGH CURRENT POSITION REQUIREMENTS ==========================
  for (let req in requirements) {
    let thisRequirements = requirements[req];
    // GET DOC CATEGORY OF THE CURRENT POSITION REQUIREMENTS ====================
    let thisCategory = documentCategories.documentCategory
      ? documentCategories.documentCategory.find(
          (item) =>
            item.user_detail_category_id ===
            thisRequirements.category.user_detail_category_id
        ) && {
          title: documentCategories.documentCategory.find(
            (item) =>
              item.user_detail_category_id ===
              thisRequirements.category.user_detail_category_id
          )!.title,
          user_detail_category_id: documentCategories.documentCategory.find(
            (item) =>
              item.user_detail_category_id ===
              thisRequirements.category.user_detail_category_id
          )!.user_detail_category_id,
        }
      : {
          title: "",
          user_detail_category_id:
            thisRequirements.category.user_detail_category_id,
        };
    // GET DOC SUB CATEGORY OF THE CURRENT POSITION REQUIREMENTS ====================
    let thisSubCategory = documentCategories.documentSubCategory
      ? documentCategories.documentSubCategory.find(
          (item) =>
            item.user_detail_subcategory_id ===
            thisRequirements.subcategory.user_detail_subcategory_id
        )
        ? {
            title: documentCategories.documentSubCategory.find(
              (item) =>
                item.user_detail_subcategory_id ===
                thisRequirements.subcategory.user_detail_subcategory_id
            )!.title,
            user_detail_subcategory_id:
              documentCategories.documentSubCategory.find(
                (item) =>
                  item.user_detail_subcategory_id ===
                  thisRequirements.subcategory.user_detail_subcategory_id
              )!.user_detail_subcategory_id,
            category_id: documentCategories.documentSubCategory.find(
              (item) =>
                item.user_detail_subcategory_id ===
                thisRequirements.subcategory.user_detail_subcategory_id
            )!.category_id,
          }
        : {
            title: "",
            user_detail_subcategory_id:
              thisRequirements.subcategory.user_detail_subcategory_id,
            category_id: "",
          }
      : {
          title: "",
          user_detail_subcategory_id:
            thisRequirements.subcategory.user_detail_subcategory_id,
          category_id: "",
        };
    // LOOP THROUGH FIELDS OF THE CURRENT POSITION REQUIREMENTS ==========================
    if (thisCategory) {
      for (let field in thisRequirements.position_requirement) {
        // GET FIELDS OF THE CURRENT POSITION REQUIREMENTS ====================
        let thisField = documentCategories.documentFields
          ? documentCategories.documentFields.find(
              (item) =>
                item._id === thisRequirements.position_requirement[field]._id
            )
            ? {
                title: documentCategories.documentFields.find(
                  (item) =>
                    item._id ===
                    thisRequirements.position_requirement[field]._id
                )!.title,
                _id: documentCategories.documentFields.find(
                  (item) =>
                    item._id ===
                    thisRequirements.position_requirement[field]._id
                )!._id,
                required: thisRequirements.position_requirement[field].required,
              }
            : {
                title: "",
                _id: thisRequirements.position_requirement[field]._id,
                required: thisRequirements.position_requirement[field].required,
              }
          : {
              title: "",
              _id: thisRequirements.position_requirement[field]._id,
              required: thisRequirements.position_requirement[field].required,
            };
        // ADD CURRENT CATEGORY, SUB CATEGORY & FIELDS OF THE CURRENT POSITION REQUIREMENTS IN POSITIONS TEMP ====================
        PositionsTemp.push({
          category_id: thisCategory.user_detail_category_id,
          category_title: thisCategory.title,
          subcategory_id: thisSubCategory.user_detail_subcategory_id,
          subcategory_title: thisSubCategory.title,
          subcategory_required: thisRequirements.required,
          field_title: thisField.title,
          field_id: thisField._id,
          required: thisField.required,
        });
      }

      // ADD CATEOGRY OF THE CURRENT POSITION REQUIREMENTS TO ARRAY ====================
      if (
        !DistinctCategoriesIds.includes(thisCategory.user_detail_category_id)
      ) {
        DistinctCategoriesIds.push(thisCategory.user_detail_category_id);
        DistinctCategoriesTemp.push({
          category_id: thisCategory.user_detail_category_id,
          category_title: thisCategory.title,
          subcategory_id: "",
          subcategory_title: "",
          subcategory_required: thisRequirements.required,
          field_title: "",
          field_id: "",
          required: false,
        });
      }

      // ADD SUB CATEOGRY OF THE CURRENT POSITION REQUIREMENTS TO ARRAY ====================
      if (
        !DistinctSubCategoriesIds.includes(
          thisSubCategory.user_detail_subcategory_id
        )
      ) {
        DistinctSubCategoriesIds.push(
          thisSubCategory.user_detail_subcategory_id
        );
        DistinctSubCategoriesTemp.push({
          category_id: thisSubCategory.category_id,
          category_title: "",
          subcategory_id: thisSubCategory.user_detail_subcategory_id,
          subcategory_title: thisSubCategory.title,
          subcategory_required: thisRequirements.required,
          field_title: "",
          field_id: "",
          required: false,
        });
      }
    }
  }
  // DistinctCategoriesIds.;

  return { PositionsTemp, DistinctCategoriesTemp, DistinctSubCategoriesTemp };
};

enum viewTabs {
  profile = "Applicant Profile",
  application = "Application Details",
}

type AdminClaimAppViewProps = {
  thisApplication: allClaimApplicationInterface;
  thisPosition: PositionAPI;
  documentCategories: DocumentReducerInterface;
  thisClaim: CandidateClaimInterface;
  claimTypes: ClaimTypesInterfaceAPI[];
  systemPositions: PositionAPI[];
  claimReasons: claimReasonInterface[] | null;

  // REDUX ==================
  auth: Auth;
  reAssessApplication: Function;
  evaluateClaim: Function;
  PushPopupAlert: Function;
};

const AdminClaimAppView: React.FC<AdminClaimAppViewProps> = (props) => {
  const [viewPosition, setViewPosition] = useState<boolean>(true);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [actionTaken, setActionTaken] = useState<ClaimDecisionStatus | null>(
    null
  );
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [responseMsg, setResponseMsg] = useState<string>("");
  const [selectedTab, setSelectedTab] = useState<viewTabs>(viewTabs.profile);

  const handleVerify = async () => {
    if (!actionTaken) {
      setFormError({
        element: "action",
        msg: "Action is required!",
      });
      return setLoadingRequest(false);
    }

    if (isEmptyOrSpaces(responseMsg)) {
      setFormError({
        element: "comment",
        msg: "Reason of rejection is required",
      });
      return setLoadingRequest(false);
    }

    let subClaim = props.thisClaim.claims.find(
      (item) => item.level === props.thisClaim.claim_level
    );

    if (subClaim) {
      setLoadingRequest(true);
      const api_body: {
        sub_claim_id: string;
        evaluation_comment: string;
        evaluated_by: string;
        decision_taken_status: ClaimDecisionStatus;
      } = {
        sub_claim_id: subClaim.id,
        evaluation_comment: responseMsg,
        evaluated_by: props.auth.user.user_id,
        decision_taken_status: actionTaken,
      };

      try {
        const res = await axios.put<CandidateClaimInterface>(
          `${API.CLAIMING}/user/evaluate/${props.thisClaim._id}`,
          api_body
        );
        if (res) {
          console.log({ evaluate_claim_res: res });
          props.evaluateClaim(res.data);
          // REVALIDATED DOCUMENT IF IT IS APPROVED =========================
          if (actionTaken === ClaimDecisionStatus.ACCEPTED) {
            await overrideDecision();
          }
          props.PushPopupAlert({
            title: `Claim responded successfully!`,
            theme: PopupAlertTheme.success,
          });
          // setDocumentId("null");
          setResponseMsg("");
          setActionTaken(null);
        }
      } catch (error) {
        console.error(error);
        props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setLoadingRequest(false);
    }
  };

  const getReasonById = (
    reason_id: string
  ): claimReasonInterface | undefined => {
    if (props.claimReasons) {
      return props.claimReasons.find((item) => item.id === reason_id);
    }
    return undefined;
  };

  const overrideDecision = async () => {
    try {
      const res = await axios.patch(
        `${API.APPLICATION}/tolerateappl/${props.thisClaim.claiming_on_id}`,
        {
          user_id: props.auth.user.user_id,
          comment: responseMsg,
          application_status: ApplicationStates.Shortlisted,
        }
      );

      res.data &&
        props.reAssessApplication({
          application_id: props.thisClaim.claiming_on_id,
          application_status: ApplicationStates.Shortlisted,
        });
    } catch (error) {
      console.error({ code_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      // errorToText(error);
    }
    setLoadingRequest(false);
  };

  const getPositionById = (position_id: string): PositionAPI | undefined => {
    let result = props.systemPositions.find(
      (item) => item.position_id === position_id
    );

    return result;
  };

  return (
    <section className="">
      {/* #################### VIEW TABS ################################## */}
      <div className="flex flex-row items-center mx-4">
        {Object.values(viewTabs).map((item, i) => (
          <div
            key={i}
            onClick={() => setSelectedTab(item)}
            className={`animate__animated ${
              selectedTab === item
                ? "bg-blue-100 border-blue-600 text-blue-600 animate__zoomIn"
                : "bg-white border-white text-gray-600"
            } hover:bg-blue-100 px-4 py-2 cursor-pointer font-bold border-b-4 hover:border-blue-600 hover:text-blue-600 rounded-t mt-1 mr-1`}
          >
            {item}
          </div>
        ))}
      </div>
      {/* #################### END VIEW TABS ################################## */}
      <div className="w-full bg-white px-4 py-1 divide-y-2 space-y-2">
        {/* DOCUMENT DETAILS ==================================================== */}
        <section className="flex flex-col">
          {/* DISPLAY POSITION ==================================================== */}
          {selectedTab === viewTabs.application && (
            <section className="w-full my-1 animate__animated animate__fadeIn rounded">
              <div className="bg-white w-full">
                <div className="text-gray-800 text-2xl font-bold my-2">
                  Position Details
                </div>
                <div className="flex items-center">
                  <div className="flex items-center gap-2">
                    <span className={`italic text-gray-600`}>Name:</span>
                    <span className="text-gray-800 font-bold text-2xl">
                      {getPositionById(props.thisApplication.position)
                        ? getPositionById(props.thisApplication.position)!
                            .position_title
                        : "Position not found"}
                    </span>
                  </div>
                </div>
                <div className="flex items-center gap-2 text-base text-gray-600">
                  <span className={`italic`}>Desc:</span>
                  <span className="font-semibold">
                    {getPositionById(props.thisApplication.position)
                      ? getPositionById(props.thisApplication.position)!
                          .description
                      : ""}
                  </span>
                </div>
                <div className="flex items-center gap-2 text-base text-gray-600">
                  <span className={`italic`}>School level:</span>
                  <span className="font-semibold">
                    {getPositionById(props.thisApplication.position)
                      ? getPositionById(props.thisApplication.position)!
                          .school_level_id.school_level_name
                      : ""}
                  </span>
                </div>
                <div className="flex items-center gap-2 text-base text-gray-600">
                  <span className={`italic`}>Institute:</span>
                  <span className="font-semibold">
                    {getPositionById(props.thisApplication.position)
                      ? getPositionById(props.thisApplication.position)!
                          .education_institute_id.education_category_title
                      : ""}
                  </span>
                </div>
                <div className="flex items-center gap-2 text-base text-gray-600">
                  <span className={`italic`}>Courses:</span>
                  <span className="flex items-center gap-2">
                    {getPositionById(props.thisApplication.position) ? (
                      getPositionById(
                        props.thisApplication.position
                      )!.courses.map((item, index) => (
                        <div
                          key={index + item.course_id}
                          className="flex items-center gap-1"
                        >
                          <span className="font-semibold">
                            {item.course_name}
                          </span>
                          <span className="text-gray-600">
                            ({item.course_code})
                          </span>
                          {index !==
                            getPositionById(props.thisApplication.position)!
                              .courses.length -
                              1 && ","}
                        </div>
                      ))
                    ) : (
                      <NoResult
                        theme={NoResultThemes.Dark}
                        value={"No courses found"}
                      />
                    )}
                  </span>
                </div>

                <div className="text-gray-800 text-2xl font-bold my-2">
                  Position Requirements
                </div>
                {getPositionById(props.thisApplication.position) ? (
                  getPositionById(props.thisApplication.position)!.requirements
                    .length > 0 ? (
                    FC_AdminApplicationPositionDistinctCategory(
                      getPositionById(props.thisApplication.position)!,
                      props.documentCategories
                    ).DistinctCategoriesTemp.map((item, index) => (
                      <div
                        className="bg-gray-100props.thisApplication.position rounded p-2"
                        key={index + item.category_id}
                      >
                        <div className="flex items-center gap-2 text-2xl text-gray-500 font-bold">
                          <MdTurnedIn />
                          <span>{item.category_title}</span>
                        </div>
                        <div className="border bg-white mt-1">
                          <ViewRequirements
                            requirements={FC_AdminApplicationPositionDistinctCategory(
                              getPositionById(props.thisApplication.position)!,
                              props.documentCategories
                            ).PositionsTemp.filter(
                              (req) => req.category_id === item.category_id
                            )}
                          />
                        </div>
                      </div>
                    ))
                  ) : (
                    <NoResult
                      theme={NoResultThemes.Dark}
                      value={"No requirements"}
                    ></NoResult>
                  )
                ) : (
                  <NoResult
                    theme={NoResultThemes.Dark}
                    value={"No requirements"}
                  ></NoResult>
                )}
              </div>
            </section>
          )}
          {/* END DISPLAY POSITION ==================================================== */}
          {/* -------------------------------------------------------------------------------- */}
          {/* DISPLAY PROFILE ==================================================== */}
          {selectedTab === viewTabs.profile && (
            <section className="w-full my-2 animate__animated animate__fadeIn rounded">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <span className={`italic text-gray-600`}>Names:</span>
                  <span className="text-gray-800 font-bold text-xl">
                    {props.thisClaim.previous.evaluater.name}
                  </span>
                </div>
                {false && (
                  <button
                    type="button"
                    className="bg-blue-200 hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
                    onClick={() => setViewPosition(!viewPosition)}
                  >
                    {viewPosition ? "Hide" : "More"}
                  </button>
                )}
              </div>
            </section>
          )}
          {/* END DISPLAY PROFILE ==================================================== */}
          {selectedTab === viewTabs.profile && (
            <div className="flex items-center">
              <div className="flex items-center gap-2">
                <span className={`italic text-gray-600`}>Position name:</span>
                <span className="text-gray-800 font-bold text-2xl">
                  {getPositionById(props.thisApplication.position)
                    ? getPositionById(props.thisApplication.position)!
                        .position_title
                    : "Position not found"}
                </span>
              </div>
            </div>
          )}
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Status:</span>
            <span
              className={`font-bold ${
                props.thisClaim.previous
                  ? props.thisClaim.previous.status ===
                    ApplicationStates.Rejected
                  : props.thisApplication.application_status ===
                    ApplicationStates.Rejected
                  ? "text-yellow-900"
                  : "text-blue-600"
              } rounded px-3 py-1`}
            >
              {props.thisClaim.previous
                ? capitalizeFirstLetter(props.thisClaim.previous.status)
                : capitalizeFirstLetter(
                    props.thisApplication.application_status
                  )}
            </span>
          </div>
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Evaluated by:</span>
            <span className="font-semibold">
              {props.thisClaim.previous.evaluater.name}
            </span>
          </div>
          {false && (
            <div className="flex items-center gap-2 text-sm text-gray-600">
              <span className={`italic`}>Session:</span>
              <span className="font-bold text-gray-900 py-1">
                {props.thisApplication.application_session}
              </span>
            </div>
          )}
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Date:</span>
            <span className="font-bold text-gray-900 py-1">
              {DATE(props.thisApplication.createdAt)}
            </span>
          </div>
          <div className="flex items-start gap-2 text-sm text-gray-600">
            <span className={`italic`}>Reason:</span>
            <span className="font-normal">
              {props.thisClaim.previous
                ? props.thisClaim.previous.reason
                : props.thisApplication.override_by.comment}
            </span>
          </div>
        </section>
        {/* END DOCUMENT DETAILS ==================================================== */}

        {/* ################################################################################ */}

        {/* CLAIM LEVEL 1 DETAILS ==================================================== */}
        {props.thisClaim.claims.length > 0 &&
          props.thisClaim.claims.map((claim, index) => (
            <section className="flex flex-col px-2 pt-3" key={index + claim.id}>
              <div className="text-gray-700 italic font-bold">
                Claim Comment (level {index + 1})
              </div>
              {/* CLAIM COMMENT ==>> LEVEL 1 ==================================================== */}
              <div className="flex flex-col items-start gap-2 mt-1 border rounded px-4 py-1 bg-gray-50">
                <div className="flex items-center gap-2 bg-blue-100 rounded py-1 px-2">
                  <MdInfoOutline />
                  {getReasonById(props.thisClaim.default_claim_resaons)
                    ? getReasonById(props.thisClaim.default_claim_resaons)!
                        .reason
                    : ""}
                </div>
                <p>{claim.reason}</p>
                <span className="text-gray-600 text-xs">
                  {showDate(claim.createdAt)}
                  {/* {showDate(claim.createdAt) +
                    " at " +
                    moment.unix(claim.date_created).format("MM/DD/YYYY")} */}
                </span>
              </div>
              {/* CLAIM COMMENT ==>> LEVEL 1 ==================================================== */}

              {isClaimEvaluated(props.thisClaim, claim.level) && (
                <section className="mt-4">
                  <span className="text-gray-700 italic font-semibold">
                    Response (level {index + 1})
                  </span>
                  <div className="border-t-2">
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Status:</span>
                      <span
                        className={`font-bold ${
                          claim.decision_taken_status ===
                          ClaimDecisionStatus.REJECTED
                            ? "text-yellow-900"
                            : "text-blue-600"
                        } rounded px-3 py-1`}
                      >
                        {capitalizeFirstLetter(
                          claim.decision_taken_status.toLocaleLowerCase()
                        )}
                      </span>
                    </div>
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Evaluated by:</span>
                      <span className="font-semibold">
                        {claim.evaluater.name}
                      </span>
                    </div>
                    <div className="flex items-start gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Reason:</span>
                      <span className="font-normal">
                        {claim.evaluation_comment}
                      </span>
                    </div>
                  </div>
                </section>
              )}
            </section>
          ))}
        {/* END CLAIM LEVEL 1 DETAILS ==================================================== */}
      </div>

      {/* FOOTER INITIAL ========================================== */}
      {!actionTaken && (
        <section className="flex items-center justify-between">
          {/* SHOWING STATUS ===================================== */}
          <div className="mx-4 mb-5 mt-3">
            <span
              className={`${
                props.thisClaim.last_claim_status === ClaimStatus.PENDING
                  ? "text-yellow-700"
                  : props.thisClaim.last_claim_status === ClaimStatus.DELETED
                  ? "text-red-700"
                  : "text-green-700"
              } italic font-semibold`}
            >
              {capitalizeFirstLetter(
                props.thisClaim.last_claim_status.toLocaleLowerCase()
              )}
            </span>
          </div>

          {props.thisClaim.last_claim_status === ClaimStatus.PENDING && (
            <div className="flex items-center gap-2 px-2">
              <button
                className={`px-4 py-2 font-semibold text-red-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-red-700 hover:bg-red-800`}
                onClick={() => {
                  setActionTaken(ClaimDecisionStatus.REJECTED);
                }}
              >
                <MdCancel className="w-5 h-5" />
                <span>Reject</span>
              </button>
              <button
                className={`px-4 py-2 font-semibold text-blue-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-blue-700 hover:bg-blue-800`}
                onClick={() => {
                  setActionTaken(ClaimDecisionStatus.ACCEPTED);
                }}
              >
                <FaRegCheckCircle className="w-5 h-5" />
                <span>Accept</span>
              </button>
            </div>
          )}
        </section>
      )}
      {/* END FOOTER INITIAL ========================================== */}

      {/* ======================================================== */}
      {/* FOOTER RESPONSE ========================================== */}
      {actionTaken && (
        <section className="my-5 overflow-x-auto">
          <div className="italic text-gray-700 text-base my-1 mx-2">
            Selected decision:{" "}
            <span
              className={`font-semibold text-lg ${
                actionTaken === ClaimDecisionStatus.ACCEPTED
                  ? "text-blue-700"
                  : "text-yellow-800"
              }`}
            >
              {actionTaken === ClaimDecisionStatus.ACCEPTED
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* right part */}
          <div className="flex flex-col md:flex-row items-end md:items-start gap-1 w-full">
            <div className="w-full px-2">
              <textarea
                placeholder={`Reason of your decision...`}
                cols={50}
                rows={3}
                className={`border border-${
                  formError && formError.element === "comment" ? "red" : "gray"
                }-400 rounded px-3 py-1 w-full focus:outline-none`}
                style={{ resize: "none" }}
                value={responseMsg}
                onChange={(e) => {
                  setResponseMsg(e.target.value);
                  setFormError(null);
                }}
                disabled={loadingRequest}
              ></textarea>
              {formError && formError.element === "comment" && (
                <p className="text-red-800">{formError.msg}</p>
              )}
            </div>
            <div className="flex flex-row md:flex-col gap-1 max-w-full px-2">
              <button
                className="bg-gray-700 hover:bg-gray-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                onClick={() => {
                  setResponseMsg("");
                  setActionTaken(null);
                  setFormError(null);
                }}
                disabled={loadingRequest}
              >
                <RiArrowGoBackFill className="w-5 h-5" />
                <span>Back</span>
              </button>
              <button
                className="bg-blue-700 hover:bg-blue-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                disabled={loadingRequest}
                onClick={() =>
                  window.confirm(
                    `Are you sure you want to ${
                      actionTaken === ClaimDecisionStatus.ACCEPTED
                        ? "approve"
                        : "rejected"
                    } this document?`
                  ) && handleVerify()
                }
              >
                {loadingRequest ? (
                  <div className="">Loading...</div>
                ) : (
                  <div className="inline-flex items-center space-x-2 gap-1">
                    <FaRegCheckCircle className="w-5 h-5" />
                    <span>Confirm</span>
                  </div>
                )}
              </button>
            </div>
          </div>
          {/* end right part */}
        </section>
      )}
      {/* END FOOTER RESPONSE ========================================== */}
    </section>
  );
};

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return {
    auth: auth,
  };
};

export default connect(mapStateToProps, {
  evaluateClaim,
  reAssessApplication,
  PushPopupAlert,
})(AdminClaimAppView);
