export const adminDisplayClaimTitle = (
  documentCategories: DocumentReducerInterface,
  candidateDocuments: UserDocumentsInterface[],
  thisClaim: CandidateClaimInterface
): string => {
  let result: string = "";

  // CHECK IF CLAIM IS FOR DOCUMENTS ===============================================
  if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
    let thisDoc = candidateDocuments.find(
      (item) => item.doc_id === thisClaim.claiming_on_id
    );
    result = thisDoc
      ? AdminClaimGetDocTitle(documentCategories, thisDoc).main
      : "Document not found";
  }
  // END CHECK IF CLAIM IS FOR DOCUMENTS ===============================================

  return result;
};

export const AdminClaimGetDocTitle = (
  documentCategories: DocumentReducerInterface,
  doc: UserDocumentsInterface
): { category: string; sub_category: string; field: string; main: string } => {
  let DocCategory: string = "",
    DocSubCategory: string = "",
    DocField: string = "",
    DocTitle: string = "";
  if (
    doc.sub_category_id &&
    doc.sub_category_id.length > 0 &&
    documentCategories.documentSubCategory
  ) {
    DocTitle = documentCategories.documentSubCategory.filter(
      (c) => c.user_detail_subcategory_id === doc.sub_category_id
    )[0].title;
    DocSubCategory = DocTitle;
  } else if (
    doc.category_id.length > 0 &&
    documentCategories.documentCategory
  ) {
    DocTitle = documentCategories.documentCategory.filter(
      (c) => c.user_detail_category_id === doc.category_id
    )[0].title;
    DocCategory = DocTitle;
  }
  if (
    doc.category_subcategory_data.length > 0 &&
    documentCategories.documentFields
  ) {
    DocField = documentCategories.documentFields.filter(
      (c) => c._id === doc.category_subcategory_data
    )[0].title;
    DocTitle += " in " + DocField;
  }

  return {
    category: DocCategory,
    sub_category: DocSubCategory,
    field: DocField,
    main: DocTitle,
  };
};

export const getApplicationById = (
  allApplications: allClaimApplicationInterface[] | null,
  systemPositions: PositionAPI[] | null,
  application_id: String
): {
  application: allClaimApplicationInterface;
  position: PositionAPI;
} | null => {
  if (allApplications) {
    let thisApplication = allApplications.find(
      (item) => item._id === application_id
    );
    if (thisApplication && systemPositions) {
      let thisPosition = systemPositions.find(
        (item) => item.position_id === thisApplication!.position
      );
      if (thisPosition) {
        return { application: thisApplication, position: thisPosition };
      }
    }
  }
  return null;
};

type AppProps = {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  systemPositions: PositionAPI[] | null;
  courses: course[] | null;
  loadCourses: Function;
  getPositions: Function;
  getClaimReasons: Function;
  getAllApplications: Function;
  getAllClaims: Function;
  getAllAnswers: Function;
  GetAllSessionPlans: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  verifyAllDocument: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

type AppState = {
  loading: boolean;
  loadingData: boolean;
  thisPeriod: SessionPlanInterface | null;
  viewClaim: boolean;
  periodChanged: boolean;
  claimId: string | null;
  claimType: string | null;
  candidateId: string | null;

  // FOR FORM ================
  viewImage: boolean;
  loadingRequest: boolean;
  actionTaken: ClaimDecisionStatus | null;
  responseMsg: string;
  formError: {
    element: string;
    msg: string;
  } | null;
};

class Index extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadingData: false,
      thisPeriod: null,
      viewClaim: false,
      periodChanged: false,
      claimId: null,
      claimType: null,
      candidateId: null,

      // FOR FORM ================
      viewImage: false,
      loadingRequest: false,
      actionTaken: null,
      responseMsg: "",
      formError: null,
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingData = (value: boolean) => {
    this.setState({ loadingData: value });
  };

  setPeriodChanged = (value: boolean) => {
    this.setState({ periodChanged: value });
  };

  setThisPeriod = (value: SessionPlanInterface | null) => {
    this.setState({ thisPeriod: value });
  };

  setViewClaim = (value: boolean) => {
    this.setState({ viewClaim: value });
  };

  // ------------------------------
  setClaimId = (value: string | null) => {
    this.setState({ claimId: value });
  };
  setClaimType = (value: string | null) => {
    this.setState({ claimType: value });
  };
  setCandidateId = (value: string | null) => {
    this.setState({ candidateId: value });
  };
  setViewImage = (value: boolean) => {
    this.setState({ viewImage: value });
  };
  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };

  setActionTaken = (value: ClaimDecisionStatus | null) => {
    this.setState({ actionTaken: value });
  };
  setResponseMsg = (value: string) => {
    this.setState({ responseMsg: value });
  };
  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  setDefaultSession = (sessions: SessionPlanInterface[]) => {
    // SETTING ACTIVE SESSION ==========================
    if (this.state.thisPeriod === null) {
      let thisSession = sessions.find((item) => item.is_active === true);
      this.setThisPeriod(thisSession ? thisSession : sessions[0]);
      this.loadsAllClaims(thisSession ? thisSession : sessions[0]);
      this.loadsApplications(thisSession ? thisSession : sessions[0]);
      this.loadsAlAnswers(thisSession ? thisSession : sessions[0]);
    }
  };

  loadsAllClaims = (thisSession: SessionPlanInterface) => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (this.props.claiming.allClaims === null) {
      this.props.getAllClaims(
        2,
        thisSession.session_plan_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    }
  };

  loadsApplications = (thisSession: SessionPlanInterface) => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (this.props.claiming.allApplications === null) {
      this.props.getAllApplications(
        thisSession.session_plan_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    }
  };

  loadsAlAnswers = (thisSession: SessionPlanInterface) => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (this.props.claiming.allAnswer === null) {
      this.props.getAllAnswers(
        thisSession.session_plan_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    }
  };

  componentDidMount = () => {
    // LOADING APPLICATION SESSION ====================================
    if (this.props.application_sessions === null) {
      this.props.GetAllSessionPlans(
        (status: boolean, msg: string, data?: SessionPlanInterface[]) => {
          this.setLoading(status);
          if (!status) {
            if (msg.length) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            if (data && data.length > 0) {
              this.setDefaultSession(data);
            }
          }
        }
      );
    } else if (this.props.application_sessions.length > 0) {
      this.setDefaultSession(this.props.application_sessions);
    }

    // LOADING DOCUMENT CATEGORIES ==========================
    if (!this.props.documentReducer.documentCategory) {
      this.props.getDocumentCategory();
      this.setLoading(false);
    }

    // LOADING DOCUMENT CLARIFICATIONS ==========================
    if (!this.props.documentReducer.documentSubCategory) {
      this.props.getDocumentSubCategory();
      this.setLoading(false);
    }

    // LOADING DOCUMENT FIELDS ==========================
    if (!this.props.documentReducer.documentFields) {
      this.props.getDocumentFields();
      this.setLoading(false);
    }

    if (this.props.systemPositions === null) {
      this.props.getPositions((status: boolean, msg: string) => {
        this.setLoading(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (this.state.loading) {
      this.setLoading(false);
    }

    if (this.props.claiming.claimReasons === null) {
      this.props.getClaimReasons((status: boolean, msg: string) => {
        this.setLoading(status);
        if (!status) {
          if (msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }

    if (
      this.props.courses === null ||
      (this.props.courses.length === 0 && this.state.loading)
    ) {
      this.props.loadCourses(this.setLoading);
      this.setLoading(false);
    } else {
      this.setLoading(false);
    }
  };

  displayClaimTitle = (
    thisClaim: CandidateClaimInterface
  ): string | JSX.Element => {
    let result: string = thisClaim.claiming_on_id;
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      let thisDocs = thisClaim.userDetails.details.filter(
        (item) => item.validation_status !== "waiting"
      );
      result =
        thisDocs.length > 0
          ? adminDisplayClaimTitle(
              this.props.documentReducer,
              thisDocs,
              thisClaim
            )
          : "Document not found";
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (this.props.claiming.allApplications) {
        let thisApplication = this.props.claiming.allApplications.find(
          (item) => item._id === thisClaim.claiming_on_id
        );
        if (
          thisApplication &&
          this.props.claiming.allApplications &&
          this.props.systemPositions
        ) {
          let thisPosition = this.props.systemPositions.find(
            (item) => item.position_id === thisApplication!.position
          );
          if (thisPosition) {
            result = thisPosition.position_title;
          } else {
            result = "Application not found";
          }
        }
      }
    }

    if (
      thisClaim.type_key === PlansTypeKeys.EXAMINATION &&
      this.props.courses &&
      this.props.claiming.allAnswer
    ) {
      let thisAnswer = getCourseIdBySheetId(
        this.props.claiming.allAnswer,
        thisClaim.claiming_on_id
      );
      if (thisAnswer && getCourseById(this.props.courses, thisAnswer.course)) {
        result =
          "Exam of " +
          getCourseById(this.props.courses, thisAnswer.course)!.course_name;
      } else {
        result = "Unknown exam";
      }
    }

    return result;
  };

  onPeriodChange = (period_id: string) => {
    this.setPeriodChanged(true);
    this.props.getAllClaims(2, period_id, (status: boolean, msg: string) => {
      !status && this.setPeriodChanged(false);
      this.setLoadingData(status);
      if (status === false && msg.length) {
        this.props.PushPopupAlert({
          title: msg,
          theme: PopupAlertTheme.danger,
        });
      }
    });
  };

  render() {
    if (this.props.application_sessions === null) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    if (this.props.application_sessions.length === 0) {
      return (
        <div className="bg-yellow-100 rounded flex flex-col items-center justify-center p-6 my-8 text-yellow-900 border border-yellow-900">
          <MdInfoOutline className="text-6xl md:text-9xl" />
          <div className="text-center flex flex-col">
            <span className="text-2xl md:text-4xl font-extrabold">
              No active session
            </span>
            <span className="text-lg">
              Please Wait for the system to publish the sessions or contact the
              administrator
            </span>
          </div>
        </div>
      );
    }

    if (this.props.claiming.allClaims === null) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    return (
      <div>
        {/* SIDE NAVIGATION */}
        {!this.state.viewClaim && (
          <SideNavigation>
            {/* FILTER SECTION */}
            <AdminFilterByType loadingClaims={this.state.loadingData} />
            {/* END FILTER SECTION */}
          </SideNavigation>
        )}
        {/* END SIDE NAVIGATION */}

        {/* 00000000000000000000000000000000000 */}

        {/* CLAIM TITLE ===================================== */}
        {!this.state.viewClaim && (
          <ClaimHeaderTitle
            headerTitle={`${
              this.props.claiming.selectedClaimType === "all"
                ? "All"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.DOCUMENT_VALIDATION
                ? "Documents"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.APPLICATION
                ? "Applications"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.EXAMINATION
                ? "Exam"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.MARKS_PUBLICATION
                ? "Marks publication"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.PLACEMENT_TRANSFER
                ? "Placements"
                : ""
            } Claiming`}
            loading={this.state.loadingData}
            loadingSession={this.state.loading}
            thisPeriod={this.state.thisPeriod}
            setThisPeriod={this.setThisPeriod}
            setLoadingSession={this.setLoadingData}
            application_sessions={this.props.application_sessions}
            onPeriodChange={this.onPeriodChange}
            periodChanged={this.state.periodChanged}
          />
        )}
        {/* END CLAIM TITLE ===================================== */}

        {/* 00000000000000000000000000000000000 */}

        {/* CLAIM CONTENT LIST ===================================== */}
        {!this.state.viewClaim && (
          <section className="bg-white w-full p-1">
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td className="px-2 py-2">Applicant names</td>
                  <td className="px-2 py-2">Claim title</td>
                  {this.props.claiming.selectedClaimType === "all" && (
                    <td className="px-2 py-2">Claim type</td>
                  )}
                  <td className="px-2 py-2">Claim status</td>
                  <td className="px-2 py-2"></td>
                </tr>
              </thead>
              <tbody>
                {!this.props.claiming.allClaims ||
                !this.props.claiming.claimTypes ||
                !this.props.claiming.allApplications ||
                !this.props.systemPositions ||
                this.state.loadingData ? (
                  <TableLoading
                    cols={
                      this.props.claiming.selectedClaimType === "all" ? 6 : 5
                    }
                  />
                ) : filterClaimsByClaimType(
                    this.props.claiming.allClaims,
                    this.props.claiming.selectedClaimType
                  ).length <= 0 ? (
                  <tr
                    className="border-b border-gray-200 group hover:bg-gray-50 cursor-pointer"
                    key="key3201"
                  >
                    <td className="px-3 py-1" colSpan={6}>
                      <NoResult
                        theme={NoResultThemes.Light}
                        value={"No claim found!"}
                      />
                    </td>
                  </tr>
                ) : (
                  filterClaimsByClaimType(
                    this.props.claiming.allClaims,
                    this.props.claiming.selectedClaimType
                  ).map((claim, index) => (
                    <tr
                      className="border-b border-gray-200 group hover:bg-gray-50 cursor-pointer"
                      key={index + claim._id}
                      onClick={() => {
                        this.setViewClaim(true);
                        this.setClaimId(claim._id);
                        this.setCandidateId(claim.user);
                        this.setClaimType(
                          whichClaimType(claim) ? whichClaimType(claim)! : null
                        );
                      }}
                    >
                      <td className="px-3 py-1">{index + 1}</td>
                      <td className="px-3 py-1 whitespace-nowrap">
                        {claim.userDetails.user_info.fname +
                          " " +
                          claim.userDetails.user_info.lname}
                      </td>
                      <td className="px-3 py-1 whitespace-nowrap">
                        {this.displayClaimTitle(claim)}
                      </td>
                      {this.props.claiming.selectedClaimType === "all" && (
                        <td className="px-3 py-1 whitespace-nowrap">
                          {whichClaimType(claim)
                            ? capitalizeFirstLetter(whichClaimType(claim)!)
                            : "-"}
                        </td>
                      )}
                      <td className="px-3 py-1 whitespace-nowrap">
                        <span
                          className={`inline-block rounded-full duration-300 text-xs font-semibold px-3 py-1 ${
                            whichClaimStatus(claim).classes
                          }`}
                        >
                          {whichClaimStatus(claim).status}
                        </span>
                      </td>
                      <td className="px-3 py-1 text-center whitespace-nowrap">
                        <button className="btn bg-blue-500 text-white group-hover:bg-blue-600">
                          View
                        </button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </section>
        )}
        {/* END CLAIM CONTENT LIST ===================================== */}

        {/* 00000000000000000000000000000000000 */}

        {/* view claim */}
        {this.state.viewClaim &&
          this.state.candidateId &&
          this.props.claiming.allClaims &&
          this.state.claimId &&
          getClaimById(this.props.claiming.allClaims, this.state.claimId) &&
          this.props.claiming.claimTypes && (
            <section className="bg-white w-full p-1 my-4">
              {/* VIEW CLAIM HEADER =============================== */}
              <section className="flex items-center gap-2 py-2 border-b">
                <button
                  className={`ml-3 bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                    this.state.loadingRequest
                      ? "cursor-not-allowed"
                      : "cursor-pointer"
                  } animate__animated animate__fadeIn`}
                  disabled={this.state.loadingRequest}
                  onClick={() => {
                    this.setViewClaim(false);
                    this.setViewImage(true);
                    this.setLoadingRequest(false);
                    this.setActionTaken(null);
                    this.setResponseMsg("");
                    this.setFormError(null);
                  }}
                >
                  <MdArrowBack className="w-7 h-7" />
                </button>
                <div className={`text-gray-600 font-extrabold text-2xl px-2`}>
                  Viewing claim
                </div>
              </section>
              {/* END VIEW CLAIM HEADER =============================== */}

              <section className="my-2">
                {/* FOR DOCUMENT ========================== */}
                {this.state.claimType &&
                  this.state.claimType.toLocaleLowerCase() ===
                    ClaimTypesInterface.DOCUMENT.toLocaleLowerCase() && (
                    <div>
                      {getClaimById(
                        this.props.claiming.allClaims!,
                        this.state.claimId!
                      )!.userDetails.details.find(
                        (item) =>
                          item.doc_id ===
                          getClaimById(
                            this.props.claiming.allClaims!,
                            this.state.claimId!
                          )!.claiming_on_id
                      ) ? (
                        <AdminClaimDocView
                          thisDoc={
                            getClaimById(
                              this.props.claiming.allClaims!,
                              this.state.claimId!
                            )!.userDetails.details.find(
                              (item) =>
                                item.doc_id ===
                                getClaimById(
                                  this.props.claiming.allClaims!,
                                  this.state.claimId!
                                )!.claiming_on_id
                            )!
                          }
                          documentCategories={this.props.documentReducer}
                          thisClaim={
                            getClaimById(
                              this.props.claiming.allClaims,
                              this.state.claimId
                            )!
                          }
                          claimTypes={this.props.claiming.claimTypes}
                          viewImage={this.state.viewImage}
                          setViewImage={this.setViewImage}
                          loadingRequest={this.state.loadingRequest}
                          setLoadingRequest={this.setLoadingRequest}
                          actionTaken={this.state.actionTaken}
                          setActionTaken={this.setActionTaken}
                          responseMsg={this.state.responseMsg}
                          setResponseMsg={this.setResponseMsg}
                          formError={this.state.formError}
                          setFormError={this.setFormError}
                          candidateId={this.state.candidateId}
                          claimReasons={this.props.claiming.claimReasons}
                        />
                      ) : (
                        <NoResult
                          theme={NoResultThemes.Dark}
                          value={"Document not found"}
                        />
                      )}
                    </div>
                  )}

                {/* FOR APPLICATION ========================== */}
                {this.state.claimType &&
                  this.state.claimType.toLocaleLowerCase() ===
                    ClaimTypesInterface.APPLICATION.toLocaleLowerCase() && (
                    <div>
                      {getApplicationById(
                        this.props.claiming.allApplications,
                        this.props.systemPositions,
                        getClaimById(
                          this.props.claiming.allClaims,
                          this.state.claimId
                        )!.claiming_on_id
                      ) ? (
                        this.props.systemPositions && (
                          <AdminClaimAppView
                            thisApplication={
                              getApplicationById(
                                this.props.claiming.allApplications,
                                this.props.systemPositions,
                                getClaimById(
                                  this.props.claiming.allClaims,
                                  this.state.claimId
                                )!.claiming_on_id
                              )!.application
                            }
                            thisPosition={
                              getApplicationById(
                                this.props.claiming.allApplications,
                                this.props.systemPositions,
                                getClaimById(
                                  this.props.claiming.allClaims,
                                  this.state.claimId
                                )!.claiming_on_id
                              )!.position
                            }
                            documentCategories={this.props.documentReducer}
                            thisClaim={
                              getClaimById(
                                this.props.claiming.allClaims,
                                this.state.claimId
                              )!
                            }
                            claimTypes={this.props.claiming.claimTypes}
                            systemPositions={this.props.systemPositions}
                            claimReasons={this.props.claiming.claimReasons}
                          />
                        )
                      ) : (
                        <NoResult
                          theme={NoResultThemes.Dark}
                          value={"Application not found"}
                        />
                      )}
                    </div>
                  )}

                {/* FOR EXAMINATION ========================== */}
                {this.state.claimType &&
                  this.state.claimType.toLocaleLowerCase() ===
                    ClaimTypesInterface.EXAMINATION.toLocaleLowerCase() && (
                    <div>
                      {this.props.claiming.allAnswer &&
                      this.props.courses &&
                      getCourseIdBySheetId(
                        this.props.claiming.allAnswer,
                        getClaimById(
                          this.props.claiming.allClaims,
                          this.state.claimId
                        )!.claiming_on_id
                      ) &&
                      getCourseById(
                        this.props.courses,
                        getCourseIdBySheetId(
                          this.props.claiming.allAnswer,
                          getClaimById(
                            this.props.claiming.allClaims,
                            this.state.claimId
                          )!.claiming_on_id
                        )!.course
                      ) ? (
                        <AdminClaimExamView
                          thisExamSheet={
                            getCourseIdBySheetId(
                              this.props.claiming.allAnswer,
                              getClaimById(
                                this.props.claiming.allClaims,
                                this.state.claimId
                              )!.claiming_on_id
                            )!
                          }
                          thisExam={
                            getCourseById(
                              this.props.courses,
                              getCourseIdBySheetId(
                                this.props.claiming.allAnswer,
                                getClaimById(
                                  this.props.claiming.allClaims,
                                  this.state.claimId
                                )!.claiming_on_id
                              )!.course
                            )!
                          }
                          thisClaim={
                            getClaimById(
                              this.props.claiming.allClaims,
                              this.state.claimId
                            )!
                          }
                          claimTypes={this.props.claiming.claimTypes}
                          claimReasons={this.props.claiming.claimReasons}
                        />
                      ) : (
                        <NoResult
                          theme={NoResultThemes.Dark}
                          value={"Application not found"}
                        />
                      )}
                    </div>
                  )}
              </section>
            </section>
          )}
        {/* end view claim */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
  claiming,
  document,
  course,
}: StoreState): {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  courses: course[] | null;
} => {
  return {
    auth: auth,
    application_sessions: sessionPlan.allSessionPlans,
    systemPositions: system_settings.systemPositions,
    claiming,
    documentReducer: document,
    courses: course.courses,
  };
};

export const AdminClaim = connect(mapStateToProps, {
  getAllClaims,
  loadCourses,
  getAllAnswers,
  getClaimReasons,
  getAllApplications,
  GetAllSessionPlans,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  getPositions,
  PushPopupAlert,
})(Index);
